import React, { Component } from 'react';
import { MDBDataTable, MDBBtn } from "mdbreact";
import './index.css';

class ButtonDropDowns extends Component {
  constructor(props) {
          super(props);
}
  state = {
    columns: [
      {
        field: 'amount',
        label: 'Amount',
        sort:'asc'
      },
      {
        field: 'purposeName',
        label: 'Purpose Name',
        sort:'asc'
      },
      {
        field: 'description',
        label: 'Description',
        sort:'asc'
      },
      {
        field: 'createdAt',
        label: 'Date',
        sort:'asc'
      }
    ],
    rows: []
  }

  componentDidMount() {
    this.getMoney();
  }

  getMoney = () => {
    fetch("http://192.168.1.13:3001/V2/api/moneyList", {
     method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    })
      .then(res => res.json())
      .then(json => {
        let rows = [];
        console.log(json);
        json.response.forEach(item => rows.push({
          purposeName: item.purposeName,
          amount: item.amount,
          description: item.description,
          createdAt:item.createdAt,
        }));

        this.setState({ rows });
      })
      .catch(err => console.error(err));
  }

  render() {
    return (
        <MDBDataTable
          striped
          bordered
          hover
          order = {["updateAt","asc"]}
          data={{ columns: this.state.columns, rows: this.state.rows }}
        />
    );
  }
};

export default ButtonDropDowns;